
body {
  margin: 0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

header {
  width: 100%;
  text-align: center;
  background-color:white;
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  position: relative;
  top: 0px;
  z-index: 10;
  font-family: cursive;
}
a {
  height: 60px;
  line-height: 60px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  cursor: pointer;
  font-size: 18px;
  color: black;
  text-decoration: none;
  width: 150px;
  display: inline-block;
&:hover{
    background-color: darken(white,5%);
}
}
.clear {
  clear: left;
}

#hello {
  height: 94vh;
  position: relative;
  background: linear-gradient(-45deg, rgba(246, 255, 0, .8), rgba(255, 0, 161, .8)),
  url(/vue-portfolio/static/img/sakura.6a74cf2.jpg);
  background-size: cover;
  text-align: center;
}
#hello h1 {
  padding-top: 25vh;
  font-size: 50px;
  color: white;
  letter-spacing: 3px;
  font-weight: normal;
  padding-right : 20px;
  padding-left : 20px;
  margin: 0;
  font-family: cursive;
}
#hello h2 {
  padding-top: 10vh;
  font-size: 25px;
  color: white;
  font-family: cursive;
}
#account {
  padding-top: 12vh;
}
#account .social-icon {
  margin: 0px 20px;
  display: inline-block;
}
#account .social-icon img {
  width: 115px;
  height: 110px;
  border-radius: 50%;
  background-color: white;
  opacity: 0.8;
}

#prof {
  height: 94vh;
  padding-top: 12vh;
  position: relative;
  text-align: center;
}
#prof h1 {
  padding-top: 10vh;
  font-size: 30px;
  letter-spacing: 3px;
  font-weight: normal;
  padding-right : 20px;
  padding-left : 20px;
  margin: 0;
  font-family: cursive;
}
table {
  padding : 50px 0;
  display: inline-block;
  text-align: left;
  font-family: cursive;
  color: #000044;
}
th {
  padding-right: 20px;
  font-weight: normal;
}
td {
  font-size : 20px;
  padding : 5px 0px;
}
{
  text-decoration: none;
}

#skill {
  height: 94vh;
  position: relative;
  color: #000044;
  font-family: 'arial unicode ms';
}
#skill .heading {
  padding-top: 3vh;
  font-size: 24px;
  font-weight: bold;
}
#skill .goal {
  font-size: 20px;
}
#skill ul {
  width: 92vh;
  margin: 0px auto;
  text-align: left;
}
#skill ul li {
  margin: 15px 0px;
}

#development {
  height: 94vh;
  color: #000044;
  font-family: cursive;
}
#development h1 {
  padding-top: 35vh;
  font-weight: bold;
}
